#sidebar
{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1100;
    min-height: 100vh;
    min-width: 100vw;
}

#sidebarContent{
    //background: #257183;
    border-right: 2px solid white;
}

#sidebarMask{
    background: rgba(21,21,21,0.5);
}

#sidebar {
    visibility: hidden;
    -webkit-transition: visibility 0s ease-in;
    transition: visibility 0s ease-in;
}
#sidebar.visible {
    visibility: visible;
    -webkit-transition: visibility 0s ease-in;
    transition: visibility 0s ease-in;
}
#sidebar.hidden {
    visibility: hidden;
    -webkit-transition: visibility 0s ease-in;
    transition: visibility 0s ease-in;
}

.sidebar-button{
    text-shadow: 1px 1px 2px rgba(21, 21, 21, 1);
    -webkit-transition: text-shadow 0.3s linear;
    transition: text-shadow 0.3s linear;
}

.sidebar-button:hover{
    text-shadow: 1px 1px 4px rgba(21, 21, 21, 1);
    -webkit-transition: text-shadow 0.3s linear;
    transition: text-shadow 0.3s linear;
}

/* Underline From Left */
.hvr-underline-from-left {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
}
.hvr-underline-from-left:before {
    text-shadow: 1px 1px 2px rgba(21, 21, 21, 1);
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    right: 100%;
    bottom: 0;
    background: white;
    height: 2px;
    -webkit-transition-property: right;
    transition-property: right;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}
.hvr-underline-from-left:hover:before, .hvr-underline-from-left:focus:before, .hvr-underline-from-left:active:before {
    right: 0;
}

/* Sidebar Options */
.sidebar-opt
{
    background: inherit;
    color: white;
    -webkit-transition: color 0.3s linear;
    transition: color 0.3s linear;
}

.sidebar-opt:hover
{
    color: #4093A8;
    -webkit-transition: color 0.3s linear;
    transition: color 0.3s linear;
}

.sidebar-active
{
    background-color: #257183;
    -webkit-transition: text-shadow 0.3s linear;
    transition: text-shadow 0.3s linear;
}

.sidebar-active:hover{
    text-shadow: 1px 1px 2px rgba(21, 21, 21, 0.5);
    -webkit-transition: text-shadow 0.3s linear;
    transition: text-shadow 0.3s linear;
}

#splash{
    background:
            -webkit-gradient( linear, left top, left bottom, from(rgba(37, 113, 131, 0.6)), to(rgba(37, 113, 131, 1))),
            url("/img/splashComp.gif");
    background:
            -webkit-linear-gradient( rgba(37, 113, 131, 0.6), rgba(37, 113, 131, 1)),
            url("/img/splashComp.gif");
    background:
            linear-gradient( rgba(37, 113, 131, 0.6), rgba(37, 113, 131, 1)),
