    background-size: cover;
    background-repeat: no-repeat;
    background-position: 20% center;
}

.fancy-highlight {
    background-image: -webkit-gradient(linear, left top, right top, color-stop(50%, #257183), color-stop(50%, white));
    background-image: -webkit-linear-gradient(left, #257183 50%, white 50%);
    background-image: linear-gradient(to right, #257183 50%, white 50%);
    background-position: 0;
    background-size: 200%;
    color: white;
    -webkit-transition: all 1s;
    transition: all 1s;
}

.fancy-highlight:hover {
    color: #257183;
    background-position: -100%;
    -webkit-transition: all 1s;
    transition: all 1s;
}

